<template>
  <div class="container-fluid">
    <nav class="navbar navbar-expand-lg navbar-light ">
      <a class="navbar-brand" href="/">TaskPool</a>
    </nav>
    <div class="row input-area">
      <div class="col-sm-12">
        <input type="text" class="form-control add-input" @keyup.enter="addNewItem" v-model="newItem">
        <button class="btn btn-primary" v-on:click="addNewItem" >add</button>
      </div>
    </div>
    <div class="row">
      <fieldset v-bind:class="{'field':fieldActive,'field-active':fieldActiveId=='todoitem'}" class="t-card col-sm-4"
                @drop="dragDrop" @dragover="dragOver" @dragenter="dragEnter" @dragleave="dragLeave" id="todoitem">
        <legend class="t-legend text-danger">待办</legend>
        <ul class="list-group">
          <i class="right-arrow"></i>
          <li class="list-group-item" v-bind:class="{'item-loss':fieldActive}" draggable="true" @dragstart="dragStart"
              @dragend="dragEnd" v-for="(item,index) in todoitem">{{item}}
            <i class="kill-item" @click="removeSelectItem('todoitem',index)"></i>
          </li>
        </ul>
      </fieldset>
      <fieldset v-bind:class="{'field':fieldActive,'field-active':fieldActiveId=='doingitem'}" class="t-card col-sm-4"
                @drop="dragDrop" @dragover="dragOver" @dragenter="dragEnter" @dragleave="dragLeave" id="doingitem">
        <legend class="t-legend text-primary">正在做</legend>
        <ul class="list-group">
          <i class="right-arrow"></i>
          <li class="list-group-item" v-bind:class="{'item-loss':fieldActive}" draggable="true" @dragstart="dragStart"
              @dragend="dragEnd" v-for="(item,index) in doingitem">{{item}}
            <i class="kill-item" @click="removeSelectItem('doingitem',index)"></i>
          </li>
        </ul>
      </fieldset>
      <fieldset v-bind:class="{'field':fieldActive,'field-active':fieldActiveId=='okitem'}" class=" t-card col-sm-4"
                @drop="dragDrop" @dragover="dragOver" @dragenter="dragEnter" @dragleave="dragLeave" id="okitem">
        <legend class="t-legend text-success">已完成</legend>
        <ul class="list-group">
          <li class="list-group-item" v-bind:class="{'item-loss':fieldActive}" draggable="true" @dragstart="dragStart"
              @dragend="dragEnd" v-for="(item,index) in okitem">{{item}}
            <i class="kill-item" @click="removeSelectItem('okitem',index)"></i>
          </li>
        </ul>
      </fieldset>
    </div>
    <history></history>
  </div>
</template>

<script>
  import "./../assets/css/base.css"
  import History from '@/components/History'


  export default {
    name: "Index",
    components: {History},
    data() {
      return {
        todoitem: [],
        doingitem: [],
        okitem: [],
        dragnow: [],
        dragenternow: "",
        fieldActive: false,
        fieldActiveId: "",
        newItem: "",
      }
    },
    components: {
      History,
    },
    mounted: function () {
      this.parseLastDay();
      this.parseToday();
    },
    methods: {
      removeItem(arr, val) {
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].trim() == val) {
            arr.splice(i, 1);
            return;
          }
        }
      },
      parseLastDay() {
        let day_last = this.dayjs().subtract(1, 'day').format("MM.DD");
        console.log("day_last",day_last);
        if (!window.localStorage.getItem(day_last + "-parse")) {
          let temp = window.localStorage.getItem(day_last + "-" + "todo");
          let temp2 = window.localStorage.getItem(day_last + "-" + "doing");
          let temp_arr = [];
          if (temp) {
            temp = temp.split(",");
            temp_arr = temp_arr.concat(temp);
          }
          if (temp2) {
            temp2 = temp2.split(",");
            temp_arr = temp_arr.concat(temp2);
          }
          this.todoitem = this.todoitem.concat(temp_arr);
          window.localStorage.setItem(day_last + "-parse", "true");
        }
      },
      parseToday() {
        let day_now = this.dayjs().format("MM.DD");
        let day_now_todo = day_now + "-" + "todo";
        let day_now_doing = day_now + "-" + "doing";
        let day_now_ok = day_now + "-" + "ok";
        let now_todo_array = window.localStorage.getItem(day_now_todo);
        let now_doing_array = window.localStorage.getItem(day_now_doing);
        let now_ok_array = window.localStorage.getItem(day_now_ok);
        if(now_todo_array){
          this.todoitem = this.todoitem.concat(now_todo_array.split(","));
        }
        if(now_doing_array){
          this.doingitem = this.doingitem.concat(now_doing_array.split(","));
        }
        if(now_ok_array){
          this.okitem = this.okitem.concat(now_ok_array.split(","));
        }
      },
      dragStart(e) {
        e.dataTransfer.setData("item", e.target.innerText);
        e.target.style.opacity=1;
        e.target.style.boxShadow="0 2px 2px 0 rgba(47, 222, 50, .14), 0 3px 1px -2px rgba(47, 222, 50, .2), 0 1px 5px 0 rgba(47, 222, 50, .12)";
        this.dragnow = e.target.parentNode.parentNode.id;
        console.log("drag now is", this.dragnow);
        this.fieldActive = true;
      },
      dragEnter(e) {
        e.preventDefault();
        if (e.target.id) {
          this.dragenternow = e.target.id;
          this.fieldActiveId = e.target.id;
        }
        console.log("enter now", this.dragenternow);
      },
      dragOver(e) {
        e.preventDefault();
      },
      dragLeave(e) {
        e.preventDefault();
      },
      dragDrop(e) {
        e.stopPropagation();
        e.preventDefault();
        let drogObj = e.dataTransfer.getData("item");
        if (drogObj) {
          if (this.dragenternow != "" && this.dragnow != "") {
            console.log(this.dragnow, "remove", drogObj);
            this.removeItem(this[this.dragnow], drogObj.trim());
            this[this.dragenternow].push(drogObj);
          }
        }
      },
      dragEnd(e) {
        e.dataTransfer.clearData();
        e.target.style.boxShadow="";
        this.dragenternow = "";
        this.dragnow = "";
        this.fieldActive = false;
        this.fieldActiveId = "";
      },
      addNewItem() {
        if (this.newItem) {
          this.todoitem.push(this.newItem);
          this.newItem = "";
        }
      },
      addLocal(type, array) {
        let prefix = this.dayjs().format("MM.DD");
        let name = prefix + "-" + type;
        window.localStorage.setItem(name, array);
      },
      removeSelectItem(arr,index){
        console.log(index);
        this[arr].splice(index,1);
      },
    },
    watch: {
      todoitem: function () {
        this.addLocal("todo", this.todoitem);
      },
      doingitem: function () {
        this.addLocal("doing", this.doingitem);
      },
      okitem: function () {
        this.addLocal("ok", this.okitem);
      },
    }
  }
</script>

<style scoped>
  @keyframes xchange {
    0% {
      opacity:1;
      transform:rotate(20deg);
    }
    20%{
      transform:scale(1.1) rotate(20deg);
    }
    50%{
      transform:scale(1.2);
    }
  }

  .input-area {
    margin: 20px 0;
  }

  .add-input {
    display: inline-block;
    width: 40%;
  }

  .list-group{
    position:relative;
    padding-right: 10px;
  }
  .list-group-item{
    position: relative;
  }
  .list-group-item:hover{
    box-shadow:0 2px 2px 0 rgba(47, 222, 50, .14), 0 3px 1px -2px rgba(47, 222, 50, .2), 0 1px 5px 0 rgba(47, 222, 50, .12);
  }

  .kill-item{
    display: block;
    position: absolute;
    right:20px;
    top:16px;
    width:20px;
    height:20px;
  }
  .kill-item:before,
  .kill-item:after{
    position: absolute;
    content: '';
    width:1.5px;
    height:16px;
    background-color: #000;
    left:9px;
    top:1px;
    opacity: 0.3;
  }
  .kill-item:before{
    transform: rotate(45deg);
  }
  .kill-item:after{
    transform: rotate(-45deg);
  }
  .kill-item:hover{
    background-color: rgba(156, 175, 23, 0.5);
    border-radius:50%;
    transition: background-color 1s;
  }
  .kill-item:hover::before{
    opacity: 1;
    background-color: #fff;
    transform: rotate(135deg) scale(0.8);
    transition:transform 1s;
  }
  .kill-item:hover::after{
    opacity: 1;
    background-color: #fff;
    transform:rotate(45deg) scale(0.8);
    transition:transform 1s,background-color 1s;
  }

  .right-arrow {
    width: 7px;
    height: 7px;
    border-top: 2px solid #000;
    border-right: 2px solid #000;
    transform: rotate(45deg);
    position: absolute;
    top: 50%;
    right: -7px;
  }

  .field {
    position: relative;
    z-index: 22;
    min-height: 300px;
  }

  .field-active {
    background-color: rgba(2, 0, 2, 0.1);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
  }

  .item-loss {
    position: relative;
    z-index: 11;
    opacity: 0.5;
  }
</style>



